<template>
    <div>  
        <div class="loginNav">
            <img src="../../assets/login/return.jpg" alt="" class="return" @click="returns">
            <p class="title f30 cfff text-center">学生人脸录入设置</p>
        </div>
        <div class="content f28 c333" style="padding:0.84rem 0 1.7rem;">
            <ul class="pl-30">
                <li class="b_line pr-30" v-for="(item,index) in list" :key="index">
                    <div>
                        <img  :src="item.head_image ? $api.fileUrl + item.head_image : head ">
                        <span>{{item.username}}</span>
                    </div>
                    <p class="f24 c999" v-if="item.face == 1">已录入</p>
                    <button class="f24 cfff" v-else @click="faceRecognition(item.id)">录入</button>
                </li>
            </ul>
        </div>
    </div>
</template>

<script>

export default {
  name: 'HelloWorld',
    data () {
        return {
            msg: 'Welcome to Your Vue.js App',
            list:[],
            head:require('../../assets/index/head.png')
        }
    },
    methods : {
        returns(){
            this.$router.go(-1);//返回上一层
        },
        getFaceInfoByClassesId(){//获取所有学生
            this.$toast.loading({
                mask: true,
                message: '加载中...',
                duration:0
            });
            this.$http({url:this.$api.class.getFaceInfoByClassesId},{classesId:JSON.parse(localStorage.getItem('zhxy_user')).classesId,sdkType:1}).then(data => {
                console.log(data);
                if(data.code == 0){
                    this.list = data.data;
                    this.$toast.clear();
                }else{
                    this.$toast(data.msg);
                }
            },err =>{
                this.$toast(data.msg);
            })
        },
        //获取所有学生
        getStudent(){
            this.$toast.loading({
                mask: true,
                message: '加载中...',
                duration:0
            });
            this.$http({url:this.$api.class.getStudent},{classesId:JSON.parse(localStorage.getItem('zhxy_user')).classesId}).then(data => {
                console.log(data);
                if(data.code == 0){
                    this.list = data.data;
                    this.$toast.clear();
                }else{
                    this.$toast(data.msg);
                }
            },err =>{
                this.$toast(data.msg);
            })
        },
        faceRecognition(id) {
            var ids = id.toString();
            var arr = ['1',id.toString(),'1','2',JSON.parse(localStorage.getItem('zhxy_user')).token];
            var that = this;
            Cordova.exec(that.successFunction, that.failFunction, "Plugin", "faceRecognition", arr);//第一个参数 type 1录入2识别 第二个参数 id , 第三个参数 1学生 2老师 3家长 ，1登录识别，第四个参数 是否是登录 1登录2不是登录
        },
        successFunction(result){
            this.getStudent();
        },
        failFunction(msg){
        },
        successCallback(msg){
            if(msg == 0){
                this.$router.push({path:'/indexs'})
            }else{
                alert('失败');
            }
        }
    },
    created() {
        this.getStudent();
    },
}
</script>

<style scoped>
     /* 修改组件样式 */
     .loginNav{
        position: fixed;
    }
    
    .content{
        line-height: 1rem;
    }
    .content img{
        width:.6rem;
        height:.6rem;
        border-radius: 50%;
        margin-right: .3rem;
    }
    li{
        display: flex;
        align-items: center;
        justify-content: space-between;
    }
    .onDuty{
        width:.7rem;
        height:.28rem;
        background:#7C88EE;
        line-height: .28rem;
        display: inline-block;
        text-align: center;
        border-radius: .05rem;
    }
    li button{
        width:.7rem;
        height:.34rem;
        border:0;
        background: #6B77FF;
        border-radius: .08rem;
        padding:0;
        line-height: .34rem;
    }
</style>




